import getWBServer from "./modules/getWBServer";
import createRoom from "./modules/createRoom";
import getPageURL, { RoleCate } from "./modules/getPageURL";
import freeRoom from "./modules/freeRoom";

export interface IReceivedAppInfo {
  appkey: string,
  token: string,
  hubId: string | number,
  role: RoleCate,
  roomName: string,
  nickname: string
}
export const joinRoom = async (data: IReceivedAppInfo): Promise<string> => {
  try {
    const { iwbConferenceUri, iwbLeaseUri } = await getWBServer();
    const { contract } = await createRoom({
      'RC-AppKey': data.appkey,
      'RC-Token': data.token,
      baseUrl: iwbLeaseUri,
      hubId: data.hubId
    })
    contract.roomName = data.roomName;
    contract.nickname = data.nickname;
    contract.role = data.role;
    contract.appKey = data.appkey;
    const { key } = await getPageURL(contract)
    return `http://${iwbConferenceUri}?key=${key}`;
  } catch (error) {
    
    return Promise.reject(error)
  }
}

export const exitRoom = async (data:{[key:string]:string}): Promise<any> => {
  try {
    const { iwbLeaseUri } = await getWBServer();
    const res = await freeRoom({
      baseUrl: iwbLeaseUri,
      hubId: data.hubId,
      adminToken: data.adminToken,
      applyId: '123',
      applyName: "test",
      reason: "nothing",
      'RC-Token': data.token,
      'RC-AppKey': data.appKey,
    })
    return res;
  } catch (error) {
    return  Promise.reject(error)
  }
}
const RongWhiteWebSdk = {
  joinRoom,
  exitRoom
}
export default RongWhiteWebSdk