const ERROR_INFO: { [key: string]: { code: number, msg: string } } = {
  /** 发送频率过快 */
  SEND_FREQUENCY_TOO_FAST: {
    code: 5000,
    msg: '发送频率过快'
  },
  /** 错误的请求 */
  REQUEST_ERROR: {
    code: 400,
    msg: '错误的请求'
  },
  /** 服务器错误 */
  SERVER_ERROR: {
    code: 500,
    msg: '服务器错误'
  },
  /** 验证码错误 */
  VERIFY_CODE_ERROR: {
    code: 1000,
    msg: '验证码错误'
  },
  /** 验证码过期 */
  VERIFY_CODE_TIMEOUT: {
    code: 2000,
    msg: '验证码过期'
  },
  /** 权限校验失败(未登录或登录凭证失效) */
  PERMISSION_DENIED: {
    code: 403,
    msg: '权限校验失败'
  },
}

const ERROR_CODE: {[key in number|string]: number|string } = {}
const ERROR_CODE_TO_INFO: {[key: number] : {code: number, msg: string}} = {}

for (const name in ERROR_INFO) {
  const info = ERROR_INFO[name]
  const { code } = info
  ERROR_CODE[code] = name
  ERROR_CODE_TO_INFO[code] = info
}



export { ERROR_CODE, ERROR_INFO, ERROR_CODE_TO_INFO }