import httpReq from "../utils/httpReq";
import { IReceivedGetPageURL } from "./getPageURL";
import { ErrorCode } from "../enums/ErrorCode";
import { ERROR_CODE } from "../const/error";


export interface IReceivedCreateRoom {
  baseUrl: string,
  'RC-Token': string,
  'RC-AppKey': string,
  hubId: string | number,
}
export interface IResponseCreateRoom {
  contract: IReceivedGetPageURL,
  serverId: string
}
export default async (data: IReceivedCreateRoom): Promise<IResponseCreateRoom> => {
  try {
    const postData = { hubId: data.hubId, "outTradeNo": "AIQGV8BC36", "name": "融云测试房间1", "loadUrl": "", "saveUrl": "", "ownerId": "1234", "ownerName": "rongyundemo", "minutes": 60, "points": 10, "delayMinutes": 5, "saveInterval": 30, "defaultBgColor": "#333333", "defaultPenColor": "#FFFFFF", "laserPen": false, "ext": { "pensettings": { "colors": ["#FFFFFF", "#000000", "#EB5757", "#47b04b", "#1194f6", "#ffc200", "#9d1bb2", "#ec1561", "#7a5547", "#99CCCC", "#003366", "#808000"] } } };
    const url = `http://${data.baseUrl}/api/v1/iwb/lease`;
    const headers = { 'RC-Token': data["RC-Token"], 'RC-AppKey': data["RC-AppKey"] };

    const { data: res, status } = await httpReq({
      url,
      method: "POST",
      headers,
      query: postData
    })
    if (status !== ErrorCode.SUCCESS) return Promise.reject({ code: status, msg: ERROR_CODE[status] || ERROR_CODE[500] })
    const { contract, serverId, code } = JSON.parse(res as string);
    if (code !== 0) return Promise.reject({ code, msg: ERROR_CODE[code] })
    return { contract, serverId }
  } catch (error) {
    return Promise.reject(error)
  }
}