export const appendUrl = (url: string, query?: { [key: string]: string | number | null }):string => {
  url = url.replace(/\?$/, '')
  if (!query) {
    return url
  }
  const searchArr = Object.keys(query).map(key => `${key}=${query[key]}`).filter(item => !!item)
  if (searchArr.length) {
    return [url, searchArr.join('&')].join('?')
  }
  return url
}