import httpReq from "../utils/httpReq";
import { IReceivedGetPageURL } from "./getPageURL";
export interface IReceivedCreateRoom {
  baseUrl: string,
  'RC-Token': string,
  'RC-AppKey': string,
  hubId: string | number,
}
export interface IResponseCreateRoom {
  contract: IReceivedGetPageURL,
  serverId: string
}

export interface IRequestFreeRoom {
  hubId: string,
  adminToken: string,
  applyId: string | '123',
  applyName: string | "test",
  reason: string | "nothing",
  'RC-Token': string,
  'RC-AppKey': string,
  baseUrl: string,
}

export default async (data: IRequestFreeRoom): Promise<boolean> => {
  try {

    const headers = { 'RC-Token': data["RC-Token"], 'RC-AppKey': data["RC-AppKey"] };

    const { data: res, status } = await httpReq({
      url: 'http://' + data.baseUrl + '/api/v1/iwb/free/',
      method: "PUT",
      headers,
      query: data
    })
    if (status === 200 && typeof res === 'string') {
      const { code } = JSON.parse(res);
      return code === 0
    }
    return Promise.reject({ res, status })
  } catch (error) {
    return Promise.reject(error)
  }
}