import httpReq from "../utils/httpReq";
import { NAVI_URL } from "../constants";
import { ErrorCode } from "../enums/ErrorCode";
import { ERROR_CODE } from "../const/error";

export interface IReturnWBServer {
  iwbConferenceUri: string,
  iwbLeaseUri: string,
}

const getWBServer = async (): Promise<IReturnWBServer> => {
  try {
    const { data, status } = await httpReq({ url: NAVI_URL })
    if(status !== ErrorCode.SUCCESS) return Promise.reject({ code: status, msg: ERROR_CODE[status] || ERROR_CODE[500] })
    const { iwbConferenceUri, iwbLeaseUri, code } = JSON.parse(data as string);
    if(code !== ErrorCode.SUCCESS)return Promise.reject({code, msg: ERROR_CODE[code]});
    return { iwbConferenceUri, iwbLeaseUri }
  } catch (error) {
    return Promise.reject(error)
  }
}
export default getWBServer